@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,700&display=swap');

html {
  box-sizing: border-box;
  font-size: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(to bottom, #263137, #2b373d, #313d43, #364349, #3c494f);
  background-repeat: no-repeat;
  height: 100%;
}

.container {
  height: 100vh;
  width: 85%;
  max-width: 600px;
  margin: auto;
  padding-top: 40px;
}

.title {
  font-size: 2.4rem;
  margin-bottom: 2px;
  text-transform: uppercase;
  background: linear-gradient(to right, #00a9b9, #6ab6d1, #a2c2dd, #cbd2e2, #e5e5e5 25%);
  background-size: 600% 600%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.username {
  margin-bottom: 20px;
  color: #d7e6e6;
}

.subtitle {
  font-size: 1.4rem;
  text-transform: capitalize;
  color: #d7e6e6;
  border-bottom: solid 1px #00a9b9;
}

.link-group {
  margin-top: 100px;
}

.link {
  display: block;
  padding: 1em 3em;
  background-color: #d7e6e6;
  margin: 15px 0;
  text-decoration: none;
  text-align: center;
  font-size: 1rem;
  text-transform: capitalize;
  color: #313d43;
  transition: 0.3s;
}

.link:hover {
  background-color: #2b373d;
  color: #d7e6e6;
}
